.root {
  display: flex;
  align-items: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0; }
  .root.reverse {
    flex-direction: row-reverse; }

.close, .rename {
  display: inline-block;
  border: none;
  background: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  outline: 0;
  color: var(--term-js-text-color);
  user-select: none;
  font-size: 0.75rem !important;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 2.5rem;
  min-height: 2.5rem; }
  .close svg, .rename svg {
    pointer-events: none;
    width: 1rem;
    height: 1rem;
    transition: fill 0.15s ease, stroke 0.15s ease;
    fill: var(--term-js-icon-button-color); }
  .close:hover svg, .rename:hover svg {
    fill: var(--term-js-icon-button-color-hover); }
  .close:active svg, .rename:active svg {
    fill: var(--term-js-icon-button-color-active); }

.inputContainer {
  display: flex;
  align-items: center;
  flex: 1;
  height: 100%; }

.input {
